export default {
  name: "thread",
  data() {
    return {
      tab: "released",
      topic_object: {}
    }
  },
  created() {
    this.show_released();
    window._t = this;
  },
  methods: {
    on_tab_event: function (e) {
      console.log("on_tab_event:", e);
      var show_method_name = "show_" + e;
      let show_method = this[show_method_name];
      if (show_method) {
        show_method()
      }
    },
    show_released: function () {
      this.$axios.get("/app/topic/list", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'released'
        }
      }).then((response) => {
        console.log("topic list:", response);
        this.topic_object['released'] = response.data;
        console.log("topic_object:", this.topic_object);
        this.$forceUpdate();

      }).catch((error) => {
        console.log("error:", error);
      });
    },
    show_verifying: function () {
      this.$axios.get("/app/topic/list", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'verifying'
        }
      }).then((response) => {
        console.log("topic list:", response);
        this.topic_object['verifying'] = response.data;
        console.log("topic_object:", this.topic_object);
        this.$forceUpdate();
      }).catch((error) => {

      });
    }
  }
}
