    return new Promise((resolve, reject) => {
      console.log('scrollBehavior to:', to, 'from:', from, ' savedPosition:', savedPosition);
      if (to.hash && to.hash.length > 0) {
        setTimeout(() => {
          resolve({x: 0, y: savedPosition.y + 100})
        }, 100)
      } else {
        setTimeout(() => {
          resolve({x: 0, y: 0})
        }, 100)
      }
    })
