export default {
  name: "userhead",
  data() {
    return {
      imageUrl: undefined,
      file: undefined
    }
  },
  props: {
    userinfo: undefined,
  },
  created() {
    this.imageUrl = this.generate_avatar_url(this.userinfo);
  },
  methods: {
    add_click: function () {
      this.$refs.file.click();
    },
    getFile: function (event) {
      this.file = event.target.files[0]
      const item = {
        name: this.file.name,
        size: this.file.size,
        file: this.file
      }
      this.html5Reader(this.file, item);
      console.log('item:', item);
      if (this.beforeAvatarUpload(item.file)) {
        this.submitForm();
      }
    },
    html5Reader(file, item) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.$set(item, 'src', e.target.result)
        this.imageUrl = item.src;
      }
      reader.readAsDataURL(file)
    },
    on_added: function (files) {
      console.log("files:", files);
    },
    handleAvatarSuccess(res, file) {
      console.log("handleAvatarSuccess:", res, file);
      this.imageUrl = URL.createObjectURL(file.raw);
      this.$message('修改头像成功！', {
        type: "error",
      });
    },
    beforeAvatarUpload(file) {
      console.log("file:", file);
      const isImage = file.type.indexOf('image') >= 0;
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isImage) {
        this.$message('上传头像图片只能是有效的图片格式！');
      }
      if (!isLt5M) {
        this.$message('上传头像图片大小不能超过 5MB!');
      }
      return isImage && isLt5M;
    },
    submitForm() {
      let formData = new FormData()
      formData.append('avatar', this.file)
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post('/upload/avatar', formData, config).then(function (res) {
        console.log(res)
        this.$message('已成功更新头像！');
      }).catch(() => {
        this.$message('更新头像失败！');


      });
    }
  }
}
